import React, { FC } from 'react';

import { Document, Font, Page } from '@react-pdf/renderer';

import { BlockPDF, BorderPDF, ImagePDF, TablePDF, TextPDF } from 'components/elements';

import { DocumentPage, ELEMENT } from 'models';

Font.register({
  family: 'Roboto',
  fonts: [
    {
      src:
        'https://cdnjs.cloudflare.com/ajax/libs/ink/3.1.10/fonts/Roboto_condensed/robotocondensed-regular-webfont.ttf',
    },
    {
      src:
        'https://cdnjs.cloudflare.com/ajax/libs/ink/3.1.10/fonts/Roboto_condensed/robotocondensed-bold-webfont.woff',
      fontWeight: 'bold',
    },
    {
      src:
        'https://cdnjs.cloudflare.com/ajax/libs/ink/3.1.10/fonts/Roboto_condensed/robotocondensed-italic-webfont.ttf',
      fontWeight: 'normal',
      fontStyle: 'italic',
    },
    {
      src:
        'https://cdnjs.cloudflare.com/ajax/libs/ink/3.1.10/fonts/Roboto_condensed/robotocondensed-bolditalic-webfont.ttf',
      fontWeight: 'bold',
      fontStyle: 'italic',
    },
  ],
} as any);

export interface DocumentPDFProps {
  pages: DocumentPage[];
}

const DocumentPDF: FC<DocumentPDFProps> = props => {
  const { pages } = props;

  return (
    <Document>
      {pages.map((page, key) => (
        <Page key={key}>
          {page.map((element, elementKey) => {
            const style: any = {
              position: 'absolute',
              top: element.position.y / 2,
              left: element.position.x / 2,
              width: element.size.width / 2,
              height: element.size.height / 2,
              ...element.style,
            };

            if (element.type === ELEMENT.BLOCK) {
              return <BlockPDF key={elementKey} style={style} />;
            }

            if (element.type === ELEMENT.BORDER) {
              return <BorderPDF key={elementKey} style={style} />;
            }

            if (element.type === ELEMENT.TEXT && 'value' in element) {
              return <TextPDF key={elementKey} style={style} value={element.value} />;
            }

            if (element.type === ELEMENT.IMAGE && 'value' in element) {
              return <ImagePDF key={elementKey} style={style} value={element.value} />;
            }

            if (element.type === ELEMENT.TABLE && 'hasFooter' in element) {
              const { data, columnsCount, hasFooter, cellsStyle } = element;

              // use a little hack
              style.top -= 4;
              delete style.height;

              return (
                <TablePDF
                  key={elementKey}
                  data={data}
                  columnsCount={columnsCount}
                  hasFooter={hasFooter}
                  style={style}
                  cellsStyle={cellsStyle}
                />
              );
            }

            return null;
          })}
        </Page>
      ))}
    </Document>
  );
};

export { DocumentPDF };
